import { profileAPI, RES_PAGE } from "./config";
import { getJSON } from "./helper";


export const state = {
  profile: {},
  search: {
    query: '',
    results: [],
    resultsPerPage: RES_PAGE,
    page: 1
  },
  total:''
};

export const loadFugitive = async function (id) {
  try {
    const data = await getJSON(`${profileAPI}${id}`);


    let profile = data;
    state.profile = {
      title: profile.title,
      uid: profile.uid,
      description: profile.description,
      details: profile.details,
      suspects: profile.suspects,
      path: profile.path,
      images: profile.images,
      files: profile.files,
      aliases: profile.aliases,
      classification: profile.classification,
      info: [
        ["Age", profile.age_range],
        ["Eyes", profile.eyes],
        ["Hair", profile.hair],
        ["Height max", profile.height_max],
        ["Height min", profile.height_min],
        ["Languages", profile.languages],
        ["Locations", profile.locations],
        ["Nationality", profile.nationality],
        ["NCIC", profile.ncic],
        ["Occupations", profile.occupations],
        ["Place of Birth", profile.place_of_birth],
        ["Possible Countries", profile.possible_countries],
        ["Possible States", profile.possible_states],
        ["Race", profile.race],
        ["Scars and Marks", profile.scars_and_marks],
        ["Sex", profile.sex],
        ["Weight", profile.weight],
      ],
      warningM: profile.warning_message,
      rewardM: profile.reward_text,
      cautions: profile.cautions,
      remarks: profile.remarks,
      url: profile.url,
      subject: profile.subjects,
      fieldOffice: profile.field_offices,
    };
    console.log(state.profile);
  } catch (err) {
    throw err;
  }
};

export const loadSearchFugitive = async function (query, page = state.search.page) {
  try {
    state.search.query = query
    
    const respond = await fetch(`https://api.fbi.gov/@wanted?pageSize=${state.search.resultsPerPage}&page=${page}&sort_order=desc&field_offices=${query}`);
    const data = await respond.json();
    console.log(respond);

    const start = (page -1) * state.search.resultsPerPage
    const end = page * state.search.resultsPerPage


    state.search.results = data.items.map((rec) => {
      return {
        title: rec.title,
        uid: rec.uid,
        path: rec.path,
        images: rec.images,
        aliases: rec.aliases,
        url: rec.url,
        subject: rec.subjects,
        audios: rec.audios,
        additional: rec.additional,
        submit: rec.submit,
        stories: rec.stories,
        fieldOffice: rec.field_offices,
        classification: rec.classification,
      };
    });
    state.total = data.total
    state.search.page = data.page
    state.search.results.slice(start, end)
    console.log(state);
  
  } catch (err) {
    throw err;
  }
};


